<script>
import Signature from '@/components/signature/index.vue'
import { callFunction } from '@/utils/api.js'

export default {
	components: {
		Signature
	},
	
	data() {
		return {
			recordId: '',
			isReviewAction: false,
			record: {
				_id: '',
				recordNo: '',
				status: '',
				operator: '',
				operatorId: '',
				operatorSign: '',
				operatorSignTime: '',
				reviewer: '',
				reviewerId: '',
				reviewerSign: '',
				reviewerSignTime: '',
				items: [],
				createTime: '',
				completeTime: '',
				rejectReason: ''
			},
			reviewerSign: '',
			rejectReason: '',
			currentUserId: ''
		}
	},
	
	computed: {
		showActions() {
			return (this.isReviewAction && this.record.status === 'pending_review') || 
			       this.record.status === 'completed'
		}
	},
	
	onLoad(options) {
		this.recordId = options.id || ''
		this.isReviewAction = options.action === 'review'
		
		const userInfo = uni.getStorageSync('userInfo')
		this.currentUserId = userInfo && userInfo._id ? userInfo._id : ''
		
		this.loadDetail()
	},
	
	methods: {
		async loadDetail() {
			uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' })
			
			try {
				const data = await callFunction('inRecords', {
					action: 'getDetail',
					data: { _id: this.recordId }
				})
				
				this.record = data
				
				// ‰∫ëÂáΩÊï∞Â∑≤ÁªèËΩ¨Êç¢‰∫ÜURLÔºåÂâçÁ´Ø‰∏çÈúÄË¶ÅÂÜçËΩ¨Êç¢
				console.log('‚úÖ Êï∞ÊçÆÂä†ËΩΩÊàêÂäü')
				console.log('Êìç‰ΩúÂëòÁ≠æÂêçURL:', this.record.operatorSign)
				console.log('Â§çÊ†∏ÂëòÁ≠æÂêçURL:', this.record.reviewerSign)
				
				uni.hideLoading()
			} catch (err) {
				console.error('Âä†ËΩΩÂ§±Ë¥•:', err)
				uni.hideLoading()
				uni.showToast({
					title: 'Âä†ËΩΩÂ§±Ë¥•',
					icon: 'none'
				})
			}
		},
		
		// ËΩ¨Êç¢Á≠æÂêçÂõæÁâáURL
		async convertSignatureUrls() {
			try {
				console.log('üîç ÂºÄÂßãËΩ¨Êç¢Á≠æÂêçÂõæÁâáURL')
				console.log('Êìç‰ΩúÂëòÁ≠æÂêçË∑ØÂæÑ:', this.record.operatorSign)
				console.log('Â§çÊ†∏ÂëòÁ≠æÂêçË∑ØÂæÑ:', this.record.reviewerSign)
				
				const fileIds = []
				
				// Êî∂ÈõÜÈúÄË¶ÅËΩ¨Êç¢ÁöÑ‰∫ëÂ≠òÂÇ®Ë∑ØÂæÑ
				if (this.record.operatorSign && this.record.operatorSign.startsWith('cloud://')) {
					fileIds.push(this.record.operatorSign)
					console.log('‚úÖ Ê∑ªÂä†Êìç‰ΩúÂëòÁ≠æÂêçÂà∞ËΩ¨Êç¢ÂàóË°®')
				}
				if (this.record.reviewerSign && this.record.reviewerSign.startsWith('cloud://')) {
					fileIds.push(this.record.reviewerSign)
					console.log('‚úÖ Ê∑ªÂä†Â§çÊ†∏ÂëòÁ≠æÂêçÂà∞ËΩ¨Êç¢ÂàóË°®')
				}
				
				if (fileIds.length === 0) {
					console.log('‚ÑπÔ∏è Ê≤°ÊúâÈúÄË¶ÅËΩ¨Êç¢ÁöÑ‰∫ëÂ≠òÂÇ®ÂõæÁâá')
					return // Ê≤°ÊúâÈúÄË¶ÅËΩ¨Êç¢ÁöÑÂõæÁâá
				}
				
				console.log('üìã ÈúÄË¶ÅËΩ¨Êç¢ÁöÑÊñá‰ª∂ÂàóË°®:', fileIds)
				
				// Ê£ÄÊü•‰∫ëÂºÄÂèëÊòØÂê¶ÂèØÁî®
				const cloud = wx.cloud || uni.cloud
				if (!cloud || !cloud.getTempFileURL) {
					console.warn('‚ö†Ô∏è ‰∫ëÂºÄÂèëAPI‰∏çÂèØÁî®ÔºåÊó†Ê≥ïËΩ¨Êç¢Á≠æÂêçÂõæÁâáURL')
					return
				}
				
				// ÊâπÈáèËé∑Âèñ‰∏¥Êó∂URL
				console.log('üîÑ Ê≠£Âú®Ë∞ÉÁî® getTempFileURL...')
				const res = await cloud.getTempFileURL({
					fileList: fileIds
				})
				
				console.log('üì¶ getTempFileURL ËøîÂõûÁªìÊûú:', res)
				
				// Êõ¥Êñ∞Á≠æÂêçÂõæÁâáURL
				if (res && res.fileList) {
					res.fileList.forEach((item) => {
						console.log('Â§ÑÁêÜÊñá‰ª∂:', item)
						if (item.fileID === this.record.operatorSign && item.tempFileURL) {
							this.record.operatorSign = item.tempFileURL
							console.log('‚úÖ Êìç‰ΩúÂëòÁ≠æÂêçURLËΩ¨Êç¢ÊàêÂäü:', item.tempFileURL)
						}
						if (item.fileID === this.record.reviewerSign && item.tempFileURL) {
							this.record.reviewerSign = item.tempFileURL
							console.log('‚úÖ Â§çÊ†∏ÂëòÁ≠æÂêçURLËΩ¨Êç¢ÊàêÂäü:', item.tempFileURL)
						}
					})
				}
				
				console.log('‚úÖ Á≠æÂêçÂõæÁâáURLËΩ¨Êç¢ÂÆåÊàê')
			} catch (err) {
				console.error('‚ùå ËΩ¨Êç¢Á≠æÂêçÂõæÁâáURLÂ§±Ë¥•:', err)
				// ËΩ¨Êç¢Â§±Ë¥•‰∏çÂΩ±ÂìçÈ°µÈù¢ÊòæÁ§∫ÔºåÂè™ÊòØÂõæÁâáÂèØËÉΩÊó†Ê≥ïÂä†ËΩΩ
			}
		},
		
		getStatusIcon(status) {
			const iconMap = {
				draft: 'üìù',
				pending_review: '‚è≥',
				completed: '‚úÖ',
				rejected: '‚ùå'
			}
			return iconMap[status] || '‚ùì'
		},
		
		getStatusText(status) {
			const statusMap = {
				draft: 'ËçâÁ®ø',
				pending_review: 'ÂæÖÂ§çÊ†∏',
				completed: 'Â∑≤ÂÆåÊàê',
				rejected: 'Â∑≤È©≥Âõû'
			}
			return statusMap[status] || status
		},
		
		async handleApprove() {
			if (!this.reviewerSign) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÁ≠æÂêç',
					icon: 'none'
				})
				return
			}
			
			uni.showModal({
				title: 'Á°ÆËÆ§ÈÄöËøá',
				content: 'Á°ÆËÆ§ÈÄöËøáÊ≠§ÂÖ•Â∫ìÂçïÂêóÔºüÈÄöËøáÂêéÂ∫ìÂ≠òÂ∞ÜËá™Âä®Â¢ûÂä†„ÄÇ',
				success: async (res) => {
					if (res.confirm) {
						uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' })
						
						try {
							await callFunction('inRecords', {
								action: 'approve',
								data: {
									_id: this.recordId,
									reviewer: 'Â§çÊ†∏Âëò',
									reviewerId: this.currentUserId,
									reviewerSign: this.reviewerSign,
									reviewerSignTime: new Date().toISOString()
								}
							})
							
							uni.hideLoading()
							uni.showToast({
								title: 'Â§çÊ†∏ÈÄöËøá',
								icon: 'success'
							})
							
							setTimeout(() => {
								uni.navigateBack()
							}, 1500)
							
						} catch (err) {
							console.error('Â§ÑÁêÜÂ§±Ë¥•:', err)
							uni.hideLoading()
							uni.showToast({
								title: 'Â§ÑÁêÜÂ§±Ë¥•',
								icon: 'none'
							})
						}
					}
				}
			})
		},
		
		async handleReject() {
			if (!this.rejectReason.trim()) {
				uni.showToast({
					title: 'ËØ∑Â°´ÂÜôÈ©≥ÂõûÂéüÂõ†',
					icon: 'none'
				})
				return
			}
			
			uni.showModal({
				title: 'Á°ÆËÆ§È©≥Âõû',
				content: 'Á°ÆËÆ§È©≥ÂõûÊ≠§ÂÖ•Â∫ìÂçïÂêóÔºü',
				success: async (res) => {
					if (res.confirm) {
						uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' })
						
						try {
							await callFunction('inRecords', {
								action: 'reject',
								data: {
									_id: this.recordId,
									reviewer: 'Â§çÊ†∏Âëò',
									reviewerId: this.currentUserId,
									rejectReason: this.rejectReason.trim()
								}
							})
							
							uni.hideLoading()
							uni.showToast({
								title: 'Â∑≤È©≥Âõû',
								icon: 'success'
							})
							
							setTimeout(() => {
								uni.navigateBack()
							}, 1500)
							
						} catch (err) {
							console.error('Â§ÑÁêÜÂ§±Ë¥•:', err)
							uni.hideLoading()
							uni.showToast({
								title: 'Â§ÑÁêÜÂ§±Ë¥•',
								icon: 'none'
							})
						}
					}
				}
			})
		},
		
		
		handleExportPDF() {
			uni.showToast({
				title: 'PDFÂØºÂá∫ÂäüËÉΩÂºÄÂèë‰∏≠',
				icon: 'none'
			})
		}
	}
}
</script>

